.box {
    border: 1px solid white;
    border-radius: 10px;
    position: relative;
    transition: background 0.3s ease-in-out, color 0.3s ease-in-out;
    width: 100%;
    height: 100%;
}

.image {
    border-radius: 10px;
    width: 100%;
    height: 100%;
    opacity: 1;
    display: block;
    transition: .4s ease;
    backface-visibility: hidden;
}

.overlay {
    border-radius: 10px;
    transition: .4s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    background: black;
    width: 100%;
    height: 100%;
}

.box:hover .image {
    opacity: 0.3;
}

.box:hover .overlay {
    opacity: 0.7;
}

